export default {
	components: {
	},
	data() {
		return {
			list: [],
			loadtext: "上拉加载更多",
			emit: 10,
			dev_id: ""
		}
	},
	onReachBottom() {
		if(this.emit > this.list.length) {
			console.log('不会再上拉了哦');
			return
		}
		this.loadtext = "加载中..."
		this.emit += 10 
		console.log("触发上拉加载", this.emit);
		this.__init()
	},
	onLoad() {
		uni.showLoading({
			title: '加载中...',
			mask: true
		});
		this.__init()
	},
	methods: {
		all(it) {
			const data = uni.getStorageSync('earn')
			data.dev_id = ''
			data.dev_name = ''
			uni.setStorageSync('earn', data)
			uni.navigateBack();
		},
		select(item) {
			const data = uni.getStorageSync('earn')
			data.dev_id = item.device_id
			data.dev_name = item.device_id
			console.log('设备信息：',item, data);
			uni.setStorageSync('earn', data)
			uni.navigateBack();
			// setTimeout(() => {
			// 	uni.navigateBack();
			// },200)
		},
		searchbtn() {
			this.__init()
		},
		async __init() {
			this.$H.post("/agent/", {
				user_id: uni.getStorageSync('uid'),
				token: uni.getStorageSync('utoken'),
				opt: "device_list",
				slimit: 0,
				elimit: this.emit,
				device_id: this.dev_id
			}).then((data) => {
				uni.hideLoading()
				this.list = data.arr
				if(this.list.length < 1) {
					uni.showToast({
						title: "暂无数据",
						icon: "none",
						duration: 2500
					})
				}
				console.log('this.list.length', this.list.length);
				// 恢复加载状态
				this.loadtext = this.list.length < this.emit ? "没有更多了" :  "上拉加载更多"
			}).catch(() => {
				console.log("catch error");
			})
		}
	}
	
	
}
